body {
  color: #fff;
  cursor: pointer;
  margin: 0;
  font-family: Poppins;
}

#startBtn, #gameOver {
  background-color: #141414bf;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
}

#gameOver {
  cursor: default;
  display: none;
}

#gameOver .content {
  border-radius: 20px;
  max-width: 300px;
  padding: 50px;
}

button, button:after {
  color: #fff;
  letter-spacing: 3px;
  background: linear-gradient(45deg, #0000 5%, #f50909 5%);
  border: 0;
  outline: #0000;
  width: 380px;
  height: 86px;
  font-family: Bebas Neue, cursive;
  font-size: 36px;
  line-height: 88px;
  position: relative;
  box-shadow: 6px 0 #31d5f1;
}

button:after {
  --slice-0: inset(50% 50% 50% 50%);
  --slice-1: inset(80% -6px 0 0);
  --slice-2: inset(50% -6px 30% 0);
  --slice-3: inset(10% -6px 85% 0);
  --slice-4: inset(40% -6px 43% 0);
  --slice-5: inset(80% -6px 5% 0);
  content: "START";
  text-shadow: -3px -3px #0529f8, 3px 3px #00e6f6;
  clip-path: var(--slice-0);
  background: linear-gradient(45deg, #0000 3%, #00e6f6 3% 5%, #f50909 5%);
  display: block;
  position: absolute;
  inset: 0;
}

button:hover:after {
  animation: 1s steps(2, end) glitch;
}

@keyframes glitch {
  0% {
    clip-path: var(--slice-1);
    transform: translate(-20px, -10px);
  }

  10% {
    clip-path: var(--slice-3);
    transform: translate(10px, 10px);
  }

  20% {
    clip-path: var(--slice-1);
    transform: translate(-10px, 10px);
  }

  30% {
    clip-path: var(--slice-3);
    transform: translate(0, 5px);
  }

  40% {
    clip-path: var(--slice-2);
    transform: translate(-5px);
  }

  50% {
    clip-path: var(--slice-3);
    transform: translate(5px);
  }

  60% {
    clip-path: var(--slice-4);
    transform: translate(5px, 10px);
  }

  70% {
    clip-path: var(--slice-2);
    transform: translate(-10px, 10px);
  }

  80% {
    clip-path: var(--slice-5);
    transform: translate(20px, -10px);
  }

  90% {
    clip-path: var(--slice-1);
    transform: translate(-10px);
  }

  100% {
    clip-path: var(--slice-1);
    transform: translate(0);
  }
}

#text-score {
  color: #fff;
  font-size: 3em;
  font-weight: bold;
  position: absolute;
  top: 20px;
  left: 20px;
}

#score {
  color: #fff;
  font-size: 3em;
  font-weight: bold;
  position: absolute;
  top: 20px;
  left: 180px;
}

